body {
  font-family: Monaco, Consolas, "Lucida Console", monospace;
  font-size: 16px;
  color: white;
  background-color: black;
}

p {
  margin: 0px;
}

.consoleInput {
  font-family: Monaco, Consolas, "Lucida Console", monospace;
  font-size: 16px;
  width: 90%;
  border: none;
  outline: none;
  background-color: black;
  color: white;
}

.consoleWindow {
  width: 35vw;
  /* border-style: dashed; */
  border-color: white;
  margin: auto;
  margin-top: 5vh;
  padding: 1em 0 1em 1em;
  max-width: 800px;
  min-width: 500px;
  overflow-x: hidden;
  margin-bottom: 20vh;
  overflow-y: auto;
  border-right: 1em solid transparent;
}

.disabled {
  color: grey;
}

.approved {
  color: lime;
}

.error {
  color: red;
}

.fine-print {
  font-size: 5px;
  margin-left: 1vw;
}

.hint {
  color: Black;
}

.inline {
  display: inline;
}

.bottomPad {
  padding-bottom: 8px;
}

p.center {
  text-align: center;
}

img.center {
  display: block;
  margin: 16px auto;
}

img.smartphone-rotate {
  transform: rotate(90deg);
}


/* SCROLLBAR */
/* width */
::-webkit-scrollbar {
  width: 1px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
